body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(right, rgba(32,178,170,0.6), rgba(255,255,224,0.6));
  background: linear-gradient(to left, rgba(32,178,170,0.6), rgba(255,255,224,0.6));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
	font-family: 'Charmonman', cursive;
	src: url('https://fonts.googleapis.com/css?family=Charmonman:700');
}

h1 {
	font-family: 'Charmonman', cursive;
	color: purple;
	text-shadow: 3px 3px lightblue;
}
